body,html{font-family:Montserrat,'Helvetica Neue',Arial,sans-serif;font-weight:100;overflow-x:hidden}a,h1,i,li,ul{-webkit-transition:.3s ease-in-out;transition:.3s ease-in-out}a{cursor:pointer;color:inherit!important}li,ul{list-style-type:none!important;margin:0!important;padding:0!important;-webkit-transition:.3s;transition:.3s}a,button,div,i,select{text-decoration:none!important;outline:0!important}section{max-width:60em;margin:auto;padding:1em 1em 0;-webkit-transition:.3s ease-in-out;transition:.3s ease-in-out}h1,h2,h3,h4,h5,h6{font-weight:300!important}ul>li{padding:.1em .5em!important}::-webkit-scrollbar{display:none}.footer{font-size:1.2em;margin:1em auto}.profile{background:url(../images/profile.jpg);height:7em;width:7em;box-shadow:0 0 .1em .1em rgba(0,0,0,.5);background-size:cover;border-radius:5em;background-position:center center;margin:1.5em auto;-webkit-transition:.3s ease-in-out;transition:.3s ease-in-out;-webkit-filter:grayscale(100%);filter:grayscale(100%)}.profile:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.header{text-align:center}.contact-links{font-size:1.7em}